<template>
    <div id="app">
        <Header></Header>
        <Breadcrumb name="创作中心" slug="publish" root="/publish" :crumbEnable="true">
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" v-bind:slot="'logo'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M311.984 34.651L0 346.636v70.711h70.71l311.985-311.985-70.711-70.711zm-253.7 352.696H30v-28.284L311.985 77.078l28.285 28.284L58.284 387.347zM0 447.349h512v30H0zM150 387.349h362v30H150z"/></svg>
        </Breadcrumb>
        <LeftSidebar>
            <Nav />
        </LeftSidebar>
        <Main :withoutRight="true" class="m-publish-container">
            <router-view class="m-publish" />
        </Main>
    </div>
</template>

<script>
import Nav from "@/components/Nav.vue";
import User from "@jx3box/jx3box-common/js/user";
import LocalDraft from "@/utils/localDraft";
import { getUserConf } from "@/service/user.js";
export default {
    name: "publish",
    props: [],
    data: function() {
        return {
        };
    },
    methods: {
        loadUserConf : function (){
            getUserConf().then((res) => {
                this.$store.commit('setUserConf',res?.data?.data)
            })
        },
        init : function (){

            // 草稿箱
            const localDraft = new LocalDraft();
            this.$store.commit("SET_DB", localDraft);

            // 用户偏好
            this.loadUserConf();

        },
    },
    created: function() {
        if (location.hostname != "localhost") {
            if (!User.isLogin()) {
                User.toLogin();
            }
        }
        this.init();
    },
    components: {
        Nav,
    },
};
</script>
<style lang="less">
@import "./assets/css/app.less";
</style>
